.form-signin {
  padding: 15px;
  margin: auto;
  text-align: center;
  overflow-wrap: break-word;
}
.form-signin .checkbox {
  font-weight: 400;
}
.form-signin .form-control {
  position: relative;
  box-sizing: border-box;
  height: auto;
  padding: 10px;
  font-size: 16px;
}
.form-signin .form-control:focus {
  z-index: 2;
}
.form-signin input[type="email"] {
  margin-bottom: -1px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}
.form-signin input[type="password"] {
  margin-bottom: 10px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}
.player
{
  text-align: left;
}
.deck
{
  height: 120px;
  width: 240px;
  margin: 12px;
  padding: 0px;
}
.game-card
{
  width: 60px;
  height: 60px;
  padding: 0px;
  margin: 0px;  
  
}